		        transform: scale(1.1);
	}
	60% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes bounce {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
